import { RenderNodeStyleProps, Controller } from '@ali/4ever-cangjie';
import { Paragraph } from '@ali/4ever-plugin-paragraph';
import { SpacingUtil, ShdUtil, defaultCompactSpacing } from '@ali/4ever-utils';


export default function getNodeStyle(
  props: RenderNodeStyleProps,
  controller: Controller,
  next: Function,
) {
  const { node, docStyle } = props;
  // 仅针对表格内的普通段落做处理
  if (!Paragraph.isParagraph(node) || !controller.query('isNodeInTable', node)) return next();
  let style: React.CSSProperties = {};
  const { pPr } = docStyle?.docDefaults?.data || {};

  // 宽松间距下的段落在表格中的间距（by designer shisi）
  style = {
    ...ShdUtil.toStyle(node),
    ...SpacingUtil.toStyle(node, pPr?.spacing ? defaultCompactSpacing : undefined),
  };

  const { lineHeight, ...rest } = style;
  return rest;
}
